@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.Message {
    width: 90%;
    max-width: 400px;
    min-height: 40px;
    margin: 20px auto;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
}

.Message-red {
    background-color: #FFCFCF;
    color: #BC0D0D;
    border: 1px solid #BC0D0D;
}

.Message-green {
    background-color: #A1EB89;
    color: #2C7712;
    border: 1px solid #2C7712;
}
.Button {
    background-color: black;
    padding: 10px 15px;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    border: none;
}

.Button-form {
    border-radius: 7px;
}

.Button:hover {
    background-color: #3E3E3E;
    cursor: pointer;
}
.Form-form {
    width: 90%;
    margin: 0 auto;
    max-width: 400px;
    display: flex;
    flex-direction: column;
}

.Form-field {
    height: 30px;
    padding-left: 10px;
    border: 1px solid #000000;
    border-radius: 7px;
    margin: 5px 0;
    font-size: 16px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.Form-btn {
    margin-top: 10px;
}
.LoginPage-message {
    text-align: center;
    font-weight: 700;
    margin-top: 10vh;
}

.Balance {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 7px;
    padding: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    justify-self: flex-end;
}
.ItemList-container {
    box-sizing: border-box;
    max-width: 550px;
    width: 90%;
    margin: 0 auto 40px auto;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
}

.ItemList-placeholder {
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Transaction-transaction {
    padding: 0 12px;
}

.Transaction-container {
    border-bottom: 1px solid #BFBFBF;
    padding: 12px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Transaction-transaction:last-of-type .Transaction-container{
    border: none;
}

.Transaction-recipient, .Transaction-amount {
    margin-bottom: 5px;
}

.Transaction-date, .Transaction-balance {
    font-size: 14px;
    color: #575757;
}

.Transaction-amount, .Transaction-balance {
    text-align: right;
}

.Transaction-recipient, .Transaction-date {
    text-align: left;
}
.TransactionPage-message {
    text-align: center;
    margin: 10vh 0 5px 0;
}

.TransactionPage-btn-container {
    display: flex;
    justify-content: space-between;
    max-width: 550px;
    width: 90%;
    margin: 10px auto;
}

.TransactionPage-account-type {
    text-align: center;
    margin: 0 0 15px 0;
    font-size: 20px;
}

.TransactionPage-close-account-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 90%;
    max-width: 550px;
}

.TransactionPage-close-btn {
    color: black;
    background-color: #F64545;
    border: none;
    margin: 10px 0 40px 0;
    padding-top: 15px;
    padding-bottom: 15px;
}

.TransactionPage-close-btn:hover {
    background-color: #F56565;
}
.SignupPage-message {
    text-align: center;
    font-weight: 700;
    margin-top: 10vh;
}
.NewTransactionForm-form {
    width: 90%;
    margin: 0 auto;
    max-width: 400px;
    display: flex;
    flex-direction: column;
}

.NewTransactionForm-field {
    height: 30px;
    padding-left: 10px;
    border: 1px solid #000000;
    border-radius: 7px;
    margin: 5px 0;
    font-size: 16px;
}

.NewTransactionForm-form-btn {
  margin-top: 10px;
}

.NewTransactionForm-disabled {
  color: #CBCBCB;
}

.NewTransactionForm-radio-container {
  margin-top: 5px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.NewTransactionForm-radio {
  margin-left: 15px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.RadioContainer-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.RadioContainer-radio {
    margin-left: 15px;
}

.RadioContainer-disabled {
    color: #CBCBCB;
}

@media (max-width:900px){
    .RadioContainer-container {
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
    }
}
.NewTransactionPage-message {
    text-align: center;
    margin: 10vh 0 30px 0;
}
.Hero-main-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 700px;
    margin: auto;
}

.Hero-coins-growing {
    border-radius: 10px;
    height: auto;
    width: 90%;
    max-width: 500px;
    margin-left: 100px;
}

.Hero-main-text {
    width: 100%;
    max-width: 300px;
    text-align: center;
    margin-right: 100px;
}

.Hero-main-container h2 {
    font-weight: 700;
    font-size: 48px;
    margin: 10px;
}

.Hero-main-container p {
    font-weight: 700;
    font-size: 18px;
    color: #7C7C7C;
}

@media (max-width: 1100px){
    .Hero-coins-growing {
        margin-left: 25px;
    }
    
    .Hero-main-text {
        margin-right: 25px;
    }
}

@media (max-width: 900px){
    .Hero-main-container {
        flex-direction: column-reverse;
        justify-content: flex-end;
    }

    .Hero-coins-growing {
        margin: 40px 0 10px 0;
    }
    
    .Hero-main-text {
        margin: 0;
    }
}
.Feature {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 160px 0 0 0;
}

.Feature:nth-of-type(2) {
    flex-direction: row-reverse;
}

.Feature .Feature-image {
    width: 90%;
    max-width: 350px;
    height: 350px;
    margin: 0 50px;
    background: #C4C4C4;
    border-radius: 10px;
}

.Feature .Feature-text {
    width: 90%;
    max-width: 350px;
    margin: 0 50px;
    text-align: center;
}

.Feature .Feature-text h3 {
    font-weight: 700;
    font-size: 36px;
    margin-bottom: 10px;
}

.Feature .Feature-text p {
    font-weight: 700;
    font-size: 18px;
    color: #7C7C7C;
}

@media (max-width: 900px){
    .Feature {
        flex-direction: column;
        margin: 80px 0 0 0;
    }

    .Feature:nth-of-type(2) {
        flex-direction: column;
    }

    .Feature .Feature-text, .Feature .Feature-image {
        margin: 0;
    }
}
.LandingPage-main {
    width: 100vw;
    height: 100vh;
    min-height: 800px;
}

.LandingPage-secondary-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin: auto;
    background-color: white;
}

.LandingPage-footer {
    text-align: center;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    box-sizing: border-box;
}

.LandingPage-join {
    margin: 160px auto 80px auto;
    text-align: center;
}

.LandingPage-join h3 {
    font-weight: 700;
    font-size: 36px;
    margin: 10px 0;
}

@media(max-width:900px) {
    .LandingPage-join {
        margin: 80px auto 40px auto;
    }
}
.AboutPage-container {
    box-sizing: border-box;
    width: 90%;
    max-width: 800px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AboutPage-title {
    margin-top: 60px;
}

.AboutPage-heart {
    color: red;
}

.AboutPage-icon-container {
    margin-top: 34px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

.AboutPage-icon-container a {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: black;
    text-decoration: none;
    margin: 0 15px;
}

.AboutPage-icon-container a:hover {
    color: #808080;
}

.AboutPage-icon-container p {
    margin: 0;
    font-size: 14px;
}

.AboutPage-icon-container p:nth-of-type(2) {
    font-size: 10px;
}

.AboutPage-icon-container .iconify {
    width: 35px;
    height: 35px;
}
.ProfilePage-message {
    text-align: center;
    margin: 7vh 0 20px 0;
}

.ProfilePage-fields {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.ProfilePage-field-title {
    text-align: center;
    margin: 5px 0;
}

.ProfilePage-field-value {
    text-align: center;
    margin: 5px 0 15px 0;
}

@media (max-width: 700px){
    .ProfilePage-fields {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }
}
.Account-account {
    display: block;
    color: black;
    text-decoration: none;
    padding: 0 12px;
}

.Account-account:hover {
    background-color: #EFEFEF;
}

.Account-container {
    border-bottom: 1px solid #BFBFBF;
    padding: 12px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Account-account:last-of-type .Account-container{
    border: none;
}

.Account-type, .Account-balance {
    margin-bottom: 5px;
}
.AccountsPage-btn-container {
    display: flex;
    justify-content: space-between;
    max-width: 550px;
    width: 90%;
    margin: 10px auto;
}

.AccountsPage-container {
    box-sizing: border-box;
    width: 90%;
    max-width: 550px;
    margin: 0 auto 40px auto;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    overflow: hidden;
}

.AccountsPage-message {
    text-align: center;
    font-weight: 700;
    margin-top: 10vh;
}

.AccountsPage-placeholder {
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.NewAccountPage-message {
    text-align: center;
    font-weight: 700;
    margin-top: 10vh;
}

.NewAccountPage-radio-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.NewAccountPage-radio {
    margin-left: 15px;
}

.NewAccountForm-form-btn {
    margin-top: 10px;
}

.NewAccountForm-form {
    width: 90%;
    margin: 0 auto;
    max-width: 400px;
    display: flex;
    flex-direction: column;
}

.NewAccountPage-disabled {
    color: #CBCBCB;
}
.Navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    z-index: 1;
}

.Navbar-brand h1 {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
}

.Navbar a {
    text-decoration: none;
    color: black;
}

.Navbar a:hover {
    text-decoration: underline;
}

.Navbar-links-right .Navbar-link {
    padding: 0 20px;
}

.Navbar-links-left .Navbar-link {
    padding: 0 20px;
}

.Navbar-burger {
    width: 25px;
    height: 20px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 13px;
    right: 15px;
}

.Navbar-burger:hover {
    cursor: pointer;
}

.Navbar-burger:hover .Navbar-bar {
    background-color: #3E3E3E;
}

.Navbar-bar {
    width: 100%;
    height: 4px;
    background-color: black;
    border-radius: 5px;
}

.Navbar-links-right, .Navbar-links-left {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Navbar-spacer {
    height: 30px;
}

@media (max-width: 900px){
    .Navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px 15px 5px 15px;
    }

    .Navbar.active {
        background-color: #DAD8D8;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    .Navbar-burger {
        display: flex;
    }

    .Navbar-links-right, .Navbar-links-left {
        display: none;
        flex-direction: column;
        width: 100%;
    }

    .Navbar-links-right a.Navbar-link, .Navbar-links-left a.Navbar-link {
        font-size: 20px;
        padding: 15px 0;
        width: 100%;
        text-align: center;
        border-bottom: 1px solid #BFBFBF;
    }

    .Navbar-links-right a:last-of-type {
        border: none;
    }

    .Navbar.active .Navbar-links-right, .Navbar.active .Navbar-links-left {
        display: flex;
    }
}

@media (min-width: 900px){
    .Navbar .Navbar-signup-btn {
        background-color: black;
        padding: 10px 15px;
        border-radius: 5px;
        color: white;
        text-decoration: none;
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
    }

    .Navbar .Navbar-signup-btn:hover {
        background-color: #3E3E3E;
        cursor: pointer;
    }
}
body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #E2F1DD;
}
